@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Slab&display=swap);
/* 'Roboto Condensed' 'Architects Daughter' 'Source Sans Pro' */

html {
  padding: 0;
  margin: 0;
  height: 100vh;
}
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  background: #f5f5f5;
  min-height: 100vh;
}
.App,
#root {
  min-height: 100vh;
}
h1 {
  font-family: "Source Sans Pro", sans-serif;
}
h2 {
  font-size: 1.6em;
  font-family: "Josefin Slab";
}
h3 {
  font-size: 14px;
  margin: 2vmin;
  font-family: "Josefin Slab";
}
p {
  font-family: "Josefin Slab", "Source Sans Pro", sans-serif;
  font-size: 1.4em;
}
a {
  /* font-family: "Josefin Slab"; */
  text-decoration: none;
  color: rgb(0, 0, 0);
  outline: none;
}
/*Pages*/

.hidden {
  display: none;
}

.page-frame {
  padding: 10px;
  padding-bottom: 60px;
  min-height: calc(100vh - 70px - 40px - 100px);
}

@media only screen and (max-width: 500px) {
  .page-frame {
    padding-bottom: 20px;
  }
}

.gallery-holder {
  padding: 0px;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
}

.underlined {
  text-decoration: underline;
}

.flex-center {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.TextField-without-border-radius .MuiInputBase-formControl {
  border-radius: 0;
}

/* general */
.mb-10 {
  margin-bottom: 10px;
}

.element-slider-show {
  opacity: 1;
  position: absolute;
  width: 100%;
  text-align: center;
  top: 45%;
  left: 0;
  transition: 2.3s;
  max-height: 100%;
  overflow: hidden;
}
.element-slider-hidden-top {
  opacity: 0;
  position: absolute;
  top: 40%;
  right: 0;
  transition: 0;
  max-height: 100%;
  overflow: hidden;
  pointer-events: none;
}
.element-slider-hidden-left {
  opacity: 0;
  position: absolute;
  left: 200;
  transition: 0.3s;
}
.slide-text {
  max-height: 100%;
  overflow: hidden;
  font-size: 3.5em;
  color: white;
  text-shadow: 3px 3px 3px black;
}
.slide-button {
  width: 500px;
  padding: 5px;
  text-align: center;
  margin: 0 auto;
  border: 5px solid white;
}
.slide-button:hover {
  transition: 0.3s;
  border-radius: 15px;
  /* background: rgba(200, 200, 200, 0.5); */
}

@media only screen and (max-width: 500px) {
  .element-slider-show {
    top: 40%;
  }
  .element-slider-hidden-top {
    top: 25%;
  }
  .slide-text {
    font-size: 2em;
  }
  .slide-button {
    width: 300px;
    max-width: 80%;
  }
}

.exhibitions-parent {
  display: block;
  text-align: center;
}
.art-show-holder {
  margin: 0 auto;
}
.art-show-card {
  display: inline-block;
  margin: 50px auto 0px auto;
  max-height: 100vh;
  max-width: 95%;
  border: 1px solid black;
  overflow: hidden;
}
.art-show-card img {
  /* display: block; */
  object-fit: contain;
  max-height: calc(100vh - 80px);
}
.art-show-title {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Roboto Condensed";
  font-size: 1.6em;
}
.exhibitions-list {
  text-align: left;
  width: 95%;
  max-width: 100%;
  margin: 50px auto 100px auto;
  padding: 20px;
  border: 1px solid black;
}
.exhibitions-list p {
  margin-bottom: 10px;
}

/*new 2020*/
.exhibitions-container {
  padding: 20px 40px 0px 40px;
}
.exhibition-viewer {
  /* height: 80vh; */
  /* min-height: 400px; */
  height: auto;
  width: 100%;
  border: 1px solid black;
  overflow: hidden;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-position: center;
  background-size: cover;
  margin-bottom: 60px;
  position: relative;
  background-color: white;
}
.exhibition-background {
  position: absolute;
  z-index: -1;
  opacity: 0.75;
  margin-left: auto;
  margin-right: auto;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
}
.exhibition-description-frame {
  background: #fafafa;
  width: 100%;
  height: auto;
  padding: 20px;
  border: 1px solid black;
  margin-bottom: 20px;
  word-wrap: break-word;
}
.exhibition-images-frame {
  /* height: 40%; */
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.side-image-frame {
  height: 100%;
  width: 30%;
  border: 1px solid black;
  overflow: hidden;
}
.side-image {
  height: 100%;
  max-width: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  object-fit: cover;
  object-position: center;
  display: block;
  cursor: pointer;
}
.expanded-image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}
.image-exit-button {
  position: absolute;
  top: 15px;
  right: 15px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  font-family: sans-serif;
  z-index: 5;
  cursor: pointer;
}
.image-exit-button:hover {
  text-shadow: 0px 0px 5px white;
}
.exhibition-subtitle {
  font-weight: bold;
}
.exhibition-desc {
  text-align: left;
}
.exhibition-copy {
  font-size: 22px;
}
.exhibition-link {
  margin-top: 10px;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: left;
  text-decoration: underline;
}
.exhibition-link:hover {
  color: blue;
}
@media screen and (max-width: 1000px) {
  .exhibition-viewer {
    flex-direction: column;
    justify-content: space-between;
    /* height: 60vh; */
  }
}
@media screen and (max-width: 700px) {
  .exhibition-viewer {
    padding: 10px;
  }
  .exhibition-title {
    font-size: 18px;
  }
  .exhibition-copy {
    font-size: 14px;
  }
  .exhibition-images-frame {
    height: 70%;
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
  .side-image-frame {
    height: 100%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    margin-bottom: 10px;
  }
  .side-image-frame:last-child {
    margin-bottom: 0;
  }
}
@media screen and (max-width: 400px) {
  .exhibition-title {
    font-size: 16px;
  }
  .exhibition-copy {
    font-size: 11px;
  }
}

.bun-story {
  position: fixed;
  background: black;
  height: 100vh;
  width: 100%;
  overflow-x: hidden;
}
.bun-story-page {
  height: 100vh;
  position: fixed;
  top: 0;
  overflow: hidden;
  margin: auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.position-relative {
  position: relative;
}
.bun-story-img {
  height: 100%;
  transition: ease-in-out 0.7s;
  margin: 0 auto;
  object-fit: cover;
  display: block;
  pointer-events: none;
}
.scroll-indicator {
  position: fixed;
  z-index: 2;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  color: white;
  top: 40vh;
  margin: auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  transition: 1s;
}
.scroll-indicator-hidden {
  opacity: 0;
  transform: translateY(-70%);
}
@media screen and (max-width: 700px) {
  .scroll-indicator {
    transform: translateX(15%);
    font-size: 18px;
  }
  .scroll-indicator-hidden {
    opacity: 0;
    transform: translate(15%, -70%);
  }
}
.scroll-indicator-down-arrow {
  position: relative;
  margin-top: 10px;
  fill: white;
  height: 40px;
  width: 40px;
  transition: ease-in-out 2s;
}
.arrow-up {
  transform: translateY(-10%);
}
.arrow-down {
  transform: translateY(10%);
}
.color-flash {
  position: fixed;
  height: 100%;
  width: 100%;
  background: white;
  z-index: 2;
  transition: 2s;
}
.color-flash-inactive {
  opacity: 0;
}
.camera-flash {
  position: fixed;
  height: 100%;
  width: 5%;
  background: rgba(255, 255, 255, 0.3);
  z-index: 2;
  transition: 0.2s;
  opacity: 0.8;
  box-shadow: 0px -100px 50vw 50vw rgba(255, 255, 255, 0.3);
  right: 0;
}
.camera-flash-inactive {
  opacity: 0;
  box-shadow: 0px 0px 0px 0vw rgba(255, 255, 255, 0.3);
}
.teaTime-pos-1 {
  transform: scale(1.3) translateX(0vw) translateY(-10vh);
}
.teaTime-pos-2 {
  transform: scale(2.5) translateX(-8%) translateY(-30%);
}
.teaTime-pos-3 {
  transform: scale(1) translateX(-0%) translateY(-0%);
}
.teaTime-pos-4 {
  transform: scale(1.8) translateX(-30%) translateY(-20%);
}
.teaTime-pos-5 {
  transform: scale(1.8) translateX(-30%) translateY(20%);
}
.teaTime-pos-6 {
  transform: scale(1.5) translateX(-0%) translateY(-0%);
}
.teaTime-pos-7 {
  transition: 3s;
  transform: scale(2.6) translateX(24%) translateY(-14%);
}
.teaTime-pos-8 {
  transition: 3s;
  opacity: 0;
