.bun-story-text-container {
  overflow: hidden;
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 20px;
  z-index: 2;
}
.story-title {
  color: white;
  font-family: "Josefin Slab", "Source Sans Pro", sans-serif;
  font-weight: bold;
  font-size: 54px;
  text-align: center;
  margin-top: 20vh;
  margin-bottom: 50px;
  text-shadow: 1px 1px 10px black;
}
.bun-story-text {
  color: white;
  font-family: "Josefin Slab", "Source Sans Pro", sans-serif;
